//   set up register function

function register(){
    fullname =  document.getElementById('fullname').value
    email =  document.getElementById('email').value
    password =  document.getElementById('password').value
    confirmp =  document.getElementById('confirmp').value


    if (email_validation(email) == false || password_validation(password) == false){
        alert(' Email or Password is Outta Line ')
    }
    if (field_validation(fullname) == false  ){
        alert('please check fullname')
        return
    }


    // moving on with the authentication 


    auth.createUserWithEmailAndPassword(email,password)
    .then(function(){
        // declare user variable
        var user = auth.currentUser 
        alert('user created !!')


        //  add user to database 
        var database_ref = database.ref


        var user_data = { 
            email : email,
            fullname : fullname,
            password:password,
            confirmp:confirmp,
            last_login : Date.now()
        }

        database_ref.child('users/' + user.uid).set(user_data)

    })

    .catch(function(error){
        // firebase will use these errors
        var error_code = error.code 
        var error_message = error.message 

        alert('error_message')

    })

}

function email_validation(email){
    expression = /^[^@]+@\w+(\.\w+)+\w$/

    if(expression.test(email) == true){
        return true
    }
    else{
        // email not good 
        return false
    }

}


function password_validation(password){
    if (password < 6){
        return false 

    }
    else{
        return true
    }
}


function field_validation(field){
    if (field == null){
        return false 
    }

    if (field.length <= 0 ){
        return false 
    }

    else 
        return true 
}
    
